body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
    background-color: whitesmoke;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.game-info {
  margin-left: 20px;
}

/*Home button*/

#home-button {
  border: 0;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 2px;
  font-weight: lighter;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  background-color: #f16051;
  color: #fff;
  font-weight: 100;
}

#home-button:active {
  outline: none;
}

#home-button {
    position: relative;
    background: -webkit-gradient(linear, left top, right top, from(#232526), to(#414345));
    background: -webkit-linear-gradient(left, #232526, #414345);
    background: -o-linear-gradient(left, #232526, #414345);
    background: linear-gradient(to right, #232526, #414345);
    margin-bottom: 20px;
}

#home-button a {
    color: white;
}

#home-button a:hover {
    text-decoration: none;
}

.help {
    min-height: calc(100vh - 100px);
}

h6 {
    height: 10px;
}


